<template>
  <div class="con">
    <!-- <div
      :style="`background-image: url(${url});background-size: 100% 100%;`"
      class="con-yi"
    ></div> -->
    <!-- <div class="home-top">
      <img src="@/assets/left-le.png" @click="goB" />  
    </div> -->
    <div class="home-top">
      <div class="home-top-l" @click="goB">
        <img src="@/assets/left-le.png" />
      </div>
      <div class="home-top-r">
        <img
          src="@/assets/icon_collect.png"
          class="home-top-r-img1"
          v-if="!contentDetail.flag"
          @click="goingCollect"
        />
        <img
          src="@/assets/icon_collected.png"
          class="home-top-r-img1"
          v-else
          @click="goingCollect"
        />
      </div>
    </div>
    <div class="home-m">
      <div class="home-m-l">
        <div class="home-m-l-t">{{ contentDetail.corporationinfo.name }}</div>
        <div class="home-m-l-end">
          {{ contentDetail.corporationinfo.corporationNatureValue }}·{{
            contentDetail.corporationinfo.corporationScaleValue
          }}·{{ contentDetail.corporationinfo.corporationLevel }}
        </div>
      </div>
      <img
        :src="contentDetail.corporationinfo.logoUrl"
        v-if="contentDetail.corporationinfo.logoUrl"
      />
      <img src="@/assets/momo.png" v-else />
    </div>
    <!-- <div class="home-tab">
      <div v-for="(item, index) in tabs" :key="index" class="home-tab-li">
        {{ item.name }}
      </div>
    </div> -->
    <div class="home-tabs">
      <div
        v-for="(item, index) in tabList"
        :key="index"
        class="home-tabs-li"
        :class="ir == index ? 'activeCss' : ''"
        @click="goChange(index)"
      >
        {{ item.name }}
      </div>
    </div>
    <div
      v-if="
        ir == 0 &&
        (contentDetail.corporationinfo.corporationShowUrl ||
          contentDetail.corporationinfo.introduction != '')
      "
      class="list-one"
    >
      <div
        class="list-one-top"
        v-if="contentDetail.corporationinfo.corporationShowUrl"
      >
        <div class="list-one-topaa">
          <img
            :src="item"
            v-for="(
              item, index
            ) in contentDetail.corporationinfo.corporationShowUrl.split(',')"
            :key="index"
            @click="toLarge(index)"
          />
        </div>
      </div>
      <div
        class="list-one-dec"
        v-if="
          isOpen == false && contentDetail.corporationinfo.introduction != ''
        "
      >
        <div
          class="list-one-dec-text"
          ref="element"
          v-html="contentDetail.corporationinfo.introduction"
        ></div>
        <div class="ab" @click="goSelect" v-if="jii != '0%'">
          <div class="ab-l">{{ tex }}</div>
          <img src="@/assets/icon-down-blue.png" class="ab-r" />
        </div>
      </div>
      <div
        class="list-one-dec1"
        :style="`display:${isPlay}`"
        v-if="contentDetail.corporationinfo.introduction != ''"
      >
        <div
          class="list-one-dec-text1"
          ref="element1"
          v-html="contentDetail.corporationinfo.introduction"
        ></div>
        <div class="ab1" @click="goSelect">
          <div class="ab1-l">收起</div>
        </div>
      </div>
    </div>
    <div
      v-if="ir == 0 && contentDetail.corporationinfo.address"
      class="list-address"
    >
      <div class="list-address-t">具体地址</div>
      <div class="mapbox">
        <div class="map" id="container"></div>
        <div class="sign"></div>
        <!-- <div class="list-address-ab" @click="handlClick(2)">
          <img src="@/assets/address-yellow.png" />
          <div class="list-address-ab-r">
            {{ contentDetail.corporationinfo.address }}
          </div>
        </div> -->
        <div class="list-address-ab">
          <img src="@/assets/address-yellow.png" />
          <div class="list-address-ab-r">
            {{ contentDetail.corporationinfo.address }}
          </div>
        </div>
      </div>
    </div>
    <div v-if="ir == 0" class="list-message">
      <div class="list-message-top">
        <div class="list-message-top-l">工商信息</div>
        <!-- <div class="list-message-top-r">法人代表：天眼查</div> -->
      </div>
      <!-- 判断有无 -->
      <div class="list-message-end">
        <span class="span1">公司全称</span>

        <span class="span2">{{ contentDetail.corporationinfo.name }}</span>
      </div>
      <div
        class="list-message-end"
        v-if="contentDetail.corporationinfo.organizationCode"
      >
        <span class="span1">组织机构代码</span>

        <span class="span2">{{
          contentDetail.corporationinfo.organizationCode
        }}</span>
      </div>
      <div
        class="list-message-end"
        v-if="contentDetail.corporationinfo.registeredCapital"
      >
        <span class="span1">注册资金</span>
        <span class="span2">
          {{ contentDetail.corporationinfo.registeredCapital }}</span
        >
      </div>
      <!-- <div class="list-message-end">
        法人代表
        <span>王某某</span>
      </div> -->
    </div>

    <div v-if="ir == 1" class="list-two">
      <div
        v-for="(item, index) in contentDetail.recruitmentinfos.list"
        :key="index"
        class="list-two-li"
        @click="toDetail(item.id)"
      >
        <img
          :src="contentDetail.corporationinfo.logoUrl"
          v-if="contentDetail.corporationinfo.logoUrl"
          class="list-two-li-img"
        />
        <img src="@/assets/momo.png" class="list-two-li-img" v-else />
        <div class="list-two-li-r">
          <div class="list-two-li-r-head">{{ item.title }}</div>
          <div class="list-two-li-r-neck" :style="`color:${bgc}`">
            {{ contentDetail.corporationinfo.name }}
          </div>
          <div class="list-two-li-r-end">
            <div class="list-two-li-r-end-l">
              {{ item.positionNum }}个职位 | {{ item.education }}
            </div>
            <div class="list-two-li-r-end-r">
              {{ item.startTime.slice(0, 10) }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- <div v-if="ir == 2" class="list-three">
      <div v-for="(item, index) in 5" :key="index" class="list-three-li">
        <img src="@/assets/status-up.png" alt="" class="list-three-li-status" />
        <div class="list-three-li-t">航空工业昌飞2022届本硕博专场招聘</div>
        <div class="list-three-li-m">
          <img src="@/assets/gray.png" />
          <div class="list-three-li-m-r">航空工业昌飞2022届本硕博专场招聘</div>
        </div>
        <div class="list-three-li-end">
          <img src="@/assets/timehei.png" />
          <div class="list-three-li-end-r">2021.07.20</div>
        </div>
      </div>
    </div> -->

    <!-- <div @click="handlClick(1)">打开高德地图app</div>

    <div @click="handlClick(2)">打开百度地图</div> -->

    <!-- 关注公司 -->
    <div class="guan-foot">
      <img
        src="@/assets/btn-look.png"
        v-if="!contentDetail.flag"
        @click="goingCollect"
      />
      <img src="@/assets/btn-looked.png" v-else @click="goingCollect" />
    </div>
  </div>
</template>

<script>
import { ImagePreview } from "vant";
import { location } from "../utils/Location";
import qs from "qs";
export default {
  name: "zhaoCompany",
  data() {
    return {
      tabs: [
        { name: "五险一金" },
        { name: "带薪年假" },
        { name: "五险一金" },
        { name: "带薪年假" },
        { name: "五险一金" },
        { name: "带薪年假" },
      ],
      tabList: [{ name: "公司介绍" }, { name: "招聘信息" }],
      ir: 0,
      dec: "赢家监管所带来",
      isOpen: false,
      isPlay: "block",
      tex: "",
      center: [], //经度+纬度
      marker: "",
      loading: false,
      url: require("../assets/yiyi.png"),
      bgc: this.GLOBAL.bgc,
      companyId: "",
      contentDetail: {},
      StartXY: {
        lng: "",
        lat: "",
        city: "北京市",
      },
      //定义终点位置
      EndXY: {
        lng: 116.545123,
        lat: 39.915599,
        city: "北京市",
      },
      jii: 9,
    };
  },
  methods: {
    toLarge(index) {
      ImagePreview({
        images:
          this.contentDetail.corporationinfo.corporationShowUrl.split(","),
        showIndex: true,
        loop: false,
        startPosition: index,
      });
    },
    handlClick(val) {
      /* Start  判断手机是IOS还是安卓 */
      let u = navigator.userAgent;
      //判断是否安卓
      let isAndroid = u.indexOf("Android") > -1 || u.indexOf("Linux") > -1;
      // 判断是否IOS
      let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      /* End */
      //判断是否点击高德地图
      if (val === 1) {
        //判断是否是安卓手机
        if (isAndroid) {
          let queryStr = `?sourceApplication=msite&lat=${this.StartXY.lat}&lon=${this.StartXY.lng}&dev=1&style=2`;
          window.location.href = `androidamap://navi${queryStr}`;
        } else if (isIOS) {
          //IOS手机————高德地图不区分ios和安卓
          let queryStr = `?sourceApplication=msite&lat=${this.StartXY.lat}&lon=${this.StartXY.lng}&dev=1&style=2`;
          window.location.href = `androidamap://navi${queryStr}`;
          let startTime = Date.now();
          let count = 0;
          let endTime = 0;
          let t = setInterval(function () {
            count += 1;
            endTime = Date.now() - startTime;
            if (endTime > 7000) {
              clearInterval(t);
            }
            if (count < 30) return;
            if (!(document.hidden || document.webkitHidden)) {
              window.location.href =
                "https://uri.amap.com/marker?position=116.545123,39.915599";
            }
          }, 200);
          window.onblur = function () {
            clearInterval(t);
          };
        }

        //百度地图
      } else if (val === 2) {
        let queryStr = `?origin=name:我的位置|latlng:${this.StartXY.lat},${this.StartXY.lng}&destination=${this.center[1]},${this.center[0]}&region=${this.contentDetail.corporationinfo.address}&coord_type=bd09ll&mode=driving`;
        if (isAndroid) {
          window.location.href = `bdapp://map/direction${queryStr}`;
          //以下代码为判断app是否已经打开
          let startTime = Date.now();
          let count = 0;
          let endTime = 0;
          let t = setInterval(function () {
            count += 1;
            endTime = Date.now() - startTime;
            //如果800毫秒后还未打开，清除定时器，直接打开浏览器端
            if (endTime > 800) {
              clearInterval(t);
            }
            if (count < 30) return;
            //（是否打开的判断逻辑为当前页面是否进入后台）
            if (!(document.hidden || document.webkitHidden)) {
              window.location.href =
                "http://api.map.baidu.com/direction" +
                queryStr +
                "&output=html";
            }

            //20为20毫秒判断一次，app是否打开
          }, 20);

          window.onblur = function () {
            clearInterval(t);
          };
        } else if (isIOS) {
          window.location.href = `baidumap://map/direction${queryStr}`;
        }
      }
    },
    // 收藏企业
    goingCollect() {
      if (!this.contentDetail.flag) {
        var kt = "add";
      } else {
        var kt = "cancle";
      }
      const params1 = {
        opt: kt,
        corporationinfoId: this.companyId,
      };
      console.log(params1);
      this.$axios
        // .get(
        //   "/a/otherCollectioninfo/ajaxsaveCollection?opt=" +
        //     kt +
        //     "&corporationinfoId=" +
        //     this.companyId
        // )
        .post(
          "/a/otherCollectioninfo/ajaxsaveCollection",
          qs.stringify(params1)
        )
        .then((res) => {
          console.log(res, "收藏");
          if (res.data.state == 1) {
            this.$toast.success(res.data.msg);
            // this.getDetailContent();
            this.getll();
          } else if (res.data.state == 4) {
            this.$toast.success(res.data.msg);
            // this.getDetailContent();
            this.getll();
          } else if (res.data.state == 2) {
            this.$toast(res.data.msg);
            // 判断去登录
            // window.open(res.data.data);
            window.location.href = res.data.data;
            this.getll();
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    goB() {
      this.$router.go(-1);
    },
    goChange(index) {
      this.ir = index;
      if (index == 0 && this.contentDetail.corporationinfo.address) {
        // setTimeout(() => {
        //   this.adMap();
        // }, 1000);
        this.getLocation();
      }
    },
    goSelect() {
      // this.isOpen = !this.isOpen;
      console.log(this.isOpen, this.isPlay);
      if (this.isOpen == false) {
        this.isOpen = true;
      } else {
        this.isOpen = false;
      }
      if (this.isPlay == "block") {
        this.isPlay = "none";
      } else {
        this.isPlay = "block";
      }

      console.log(this.isOpen, this.isPlay);
    },
    Calculation(type, num1, num2) {
      console.log(type, num1, num2);
      var temp1, temp2, a;
      try {
        // 获取temp1小数点后的长度
        temp1 = num1.toString().split(".")[1].length;
      } catch (e) {
        temp1 = 0;
      }
      try {
        // 获取temp2小数点后的长度
        temp2 = num2.toString().split(".")[1].length;
      } catch (e) {
        temp2 = 0;
      }
      // Math.max(temp1, temp2) 为了获取temp1和temp2两个值中较大的一个
      // Math.pow(a,b) 表示 a 的 b 次方
      a = Math.pow(10, Math.max(temp1, temp2));

      // 计算的方式是先将所有的小数乘为整数，待加减运算执行完之后再除去对应的 a 的值，将其变为小数输出
      // 先判断执行的方式是否是加法，不是的话则执行减法运算
      return type == "add"
        ? (num1 * a + num2 * a) / a
        : (num1 * a - num2 * a) / a;
    },
    toPercent(point) {
      console.log(point, 5);
      // var str=Number(point*100).toFixed(1);
      var str = parseInt(Number(point * 100));
      str += "%";
      return str;
    },
    adMap() {
      this.loading = true;
      //初始化地图
      var map = new AMap.Map("container", {
        zoom: 14, //缩放级别
        center: this.center, //设置地图中心点
        //resizeEnable: true,  //地图初始化加载定位到当前城市
      });

      return;
      //获取初始中心点并赋值
      var currentCenter = map.getCenter(); //此方法是获取当前地图的中心点
      this.center = [currentCenter.lng, currentCenter.lat]; //将获取到的中心点的纬度经度赋值给data的center
      console.log(this.center, "de");

      //创建标记
      this.marker = new AMap.Marker({
        position: new AMap.LngLat(currentCenter.lng, currentCenter.lat), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
      });
      // 将创建的点标记添加到已有的地图实例：
      // map.add(this.marker);

      //根据地图中心点查附近地点，此方法在下方
      // this.centerSearch();
      //监听地图移动事件，并在移动结束后获取地图中心点并更新地点列表
      var moveendFun = (e) => {
        // 获取地图中心点
        currentCenter = map.getCenter();
        this.center = [currentCenter.lng, currentCenter.lat];
        this.marker.setPosition([currentCenter.lng, currentCenter.lat]); //更新标记的位置
        //根据地图中心点查附近地点
      };
      //更新数据
      var centerSearch = () => {
        this.loading = true;
        this.centerSearch();
      };

      // 绑定事件移动地图事件
      map.on("mapmove", moveendFun); //更新标记
      map.on("moveend", centerSearch); //更新数据
    },
    centerSearch() {
      AMap.service(["AMap.PlaceSearch"], () => {
        //构造地点查询类
        var placeSearch = new AMap.PlaceSearch({
          type: "汽车服务|餐饮服务|购物服务|生活服务|体育休闲服务|医疗保健服务|住宿服务|风景名胜|商务住宅|政府机构及社会团体|科教文化服务|交通设施服务|金融保险服务|公司企业|地名地址信息", // 兴趣点类别
          pageSize: 30, // 单页显示结果条数
          pageIndex: 1, // 页码
          city: "全国", // 兴趣点城市
          autoFitView: false, // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
        });
        //根据地图中心点查附近地点
        placeSearch.searchNearBy(
          "",
          [this.center[0], this.center[1]],
          200,
          (status, result) => {
            if (status == "complete") {
              this.lists = result.poiList.pois; //将查询到的地点赋值
              this.loading = false;
            }
          }
        );
      });
    },
    toDetail(id) {
      console.log(id, "id");
      // this.$store.commit("updataDetailId", id);
      // this.$router.push({ name: "zhaoDetail" });
      this.$router.push({
        path: "/recruitmentinfoForm",
        query: { type: "", positionDetailId: id },
      });
    },
    getll() {
      let that = this;
      const params2 = {
        corporationinfoId: that.companyId,
      };
      console.log(params2);
      that.$axios
        // .get(
        //   "/f/corporationinfo/ajax_details?corporationinfoId=" + that.companyId
        // )
        .post("/f/corporationinfo/ajax_details", qs.stringify(params2))
        .then((res) => {
          console.log(res, "详情");
          if (res.data.state == 1) {
            that.contentDetail = res.data.object;
            if (that.contentDetail.corporationinfo.address) {
              that.getLocation();
            }
          } else {
            that.contentDetail = {};
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    getDetailContent() {
      let that = this;
      const params3 = {
        corporationinfoId: that.companyId,
      };

      console.log(params3);
      that.$axios
        // .get(
        //   "/f/corporationinfo/ajax_details?corporationinfoId=" + that.companyId
        // )
        .post("/f/corporationinfo/ajax_details", qs.stringify(params3))
        .then((res) => {
          console.log(res, "详情");
          if (res.data.state == 1) {
            that.contentDetail = res.data.object;
            if (that.contentDetail.corporationinfo.address) {
              that.getLocation();
            }

            if (that.contentDetail.corporationinfo.introduction != "") {
              setTimeout(() => {
                let height = that.$refs.element;
                console.log(height.offsetHeight, 6);
                let height1 = that.$refs.element1;
                console.log(height1.offsetHeight, 63);
                that.isPlay = "none";
                that.Calculation(
                  "reduce",
                  height1.offsetHeight,
                  height.offsetHeight
                );
                var temp = that.Calculation(
                  "reduce",
                  height1.offsetHeight,
                  height.offsetHeight
                );
                console.log(temp, 4);

                if (temp <= 0) {
                  var jo = "0%";
                } else {
                  that.toPercent(
                    (temp * 10000) / (height1.offsetHeight * 10000)
                  );
                  var jo = that.toPercent(
                    (temp * 10000) / (height1.offsetHeight * 10000)
                  );
                }
                console.log(jo);
                that.tex = "展开剩余" + jo;
                that.jii = jo;
              }, 0);
            }
          } else {
            that.contentDetail = {};
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    getLocation() {
      let vthis = this;
      // let geolocation = location.initMap("map-container");
      // AMap.event.addListener(geolocation, "complete", (result) => {
      //   console.log(result, "result");
      //   vthis.StartXY.lat = result.position.lat;
      //   vthis.StartXY.lng = result.position.lng;
      //   console.log(vthis.StartXY);
      // });
      const params4 = {
        key: "1786f2a0066a4c6b1c105dc85fcf7f48",
        address: vthis.contentDetail.corporationinfo.address,
      };
      console.log(params4);
      vthis.$axios
        // .get(
        //   "https://restapi.amap.com/v3/geocode/geo?key=" +
        //     "1786f2a0066a4c6b1c105dc85fcf7f48" +
        //     "&address=" +
        //     vthis.contentDetail.corporationinfo.address
        // )
        .post("https://restapi.amap.com/v3/geocode/geo", qs.stringify(params4))
        .then((res) => {
          console.log(res, "获取地址");
          if (res.data.geocodes.length != 0) {
            vthis.initAMap(res.data.geocodes[0].location);
          } else {
            var loca = "118.796877,32.060255";
            let map3 = new AMap.Map("container", {
              resizeEnable: true,
              zoom: 20,
              center: loca.split(","),
            });
            AMap.plugin("AMap.Geocoder", function () {
              let marker = new AMap.Marker({
                map: map3,
                bubble: true,
              });
            });
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    initAMap(data) {
      console.log(data, "data");
      let map = new AMap.Map("container", {
        resizeEnable: true,
        zoom: 20,
        center: data.split(","),
      });
      AMap.plugin("AMap.Geocoder", function () {
        let marker = new AMap.Marker({
          map: map,
          bubble: true,
        });
      });
    },
  },
  created() {
    // console.log(this.$store.getters.getComId, "ididid");
    // this.companyId = this.$store.getters.getComId;

    this.companyId = this.$route.query.id;
    this.getDetailContent();
  },
  mounted() {
